<template>
	<view class="content">
		<view>
		<view class="VerticalBox">
			<scroll-view class="VerticalNav nav" scroll-y scroll-with-animation :scroll-top="verticalNavTop" style="height:calc(100vh - 375upx)">
				<view class="cu-item" :class="index==tabCur?'text-purple cur':''" v-for="(item,index) in tabName" :key="index" @tap="TabSelect"
				 :data-id="index">
					{{item}}
				</view>
			</scroll-view>
			<view>
				<view v-if="tabContentId ==0"> 
							
				</view>
				</view>
			<view>
				<view v-if="tabContentId == 1">222</view>
			</view>
			<view>
				<view v-if="tabContentId == 2">333</view>
			</view>
			<view>
				<view v-if="tabContentId == 3">444</view>
			</view>
		</view>			
		</view>
		</view>

</template>

<script>
	export default {
		data() {
			return {
				tabName: ["1","2","3","4"],
				tabContentId:0,
				tabCur: 0,
				mainCur: 0,
				verticalNavTop: 0,
				load: true
			};
		},
		onLoad() {
		},
		onReady() {
			// uni.hideLoading()
		},
		methods: {
			TabSelect(e) {
				this.tabCur = e.currentTarget.dataset.id;
				this.mainCur = e.currentTarget.dataset.id;
				this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
				//改变内容
				console.log(e)
				this.tabContentId = e.currentTarget.dataset.id
			},
		}
	}
</script>

<style lang="scss">
	
	@import url("../../colorui/main.css");
	@import url("../../colorui/icon.css");
	
	.VerticalNav.nav {
		width: 200upx;
		white-space: initial;
	}
	
	.VerticalNav.nav .cu-item {
		width: 100%;
		text-align: center;
		background-color: #fff;
		margin: 0;
		border: none;
		height: 50px;
		position: relative;
		  
	}
	
	.VerticalNav.nav .cu-item.cur {
		background-color: #f1f1f1;
	
	}
	
	.VerticalNav.nav .cu-item.cur::after {
		content: "";
		width: 8upx;
		height: 30upx;
		border-radius: 10upx 0 0 10upx;
		position: absolute;
		// background-color: currentColor;
		background-color: #6000ac;
		top: 0;
		right: 0upx;
		bottom: 0;
		margin: auto;
	}
	
	.VerticalBox {
		display: flex;
		
	}
	
	.VerticalMain {
		background-color: #f1f1f1;
		flex: 1;
	}
	
</style>
